<script setup>
import bitcatImage from '@/assets/bitcat.webp';
import { ref, computed } from 'vue';
import store from '@/vuex';

// Game categories
const categories = [
  { id: 'all', name: 'All Games' },
  { id: 'solo', name: 'Solo Games' },
  { id: 'pvp', name: 'PvP Games' }
];

const userAvatar = computed(() => {
  return store.getters.session?.settings?.avatar || bitcatImage;
});

const userTag = computed(() => {
  return store.getters.userTag || 'Guest';
});

const bestScore = computed(() => {
  return store.getters.bestScore;
});

const totalWins = computed(() => {
  return store.getters.totalWins;
});

const totalLosses = computed(() => {
  return store.getters.totalLosses;
});

const userLevel = computed(() => {
  let level = 1;
  let exp = store.getters.totalScore;
  
  if (exp >= 1000) {
    level = 2;
  } else if (exp >= 5000) {
    level = 3;
  } else if (exp >= 10000) {
    level = 4;
  } else if (exp >= 20000) {
    level = 5;
  } else if (exp >= 50000) {
    level = 6;
  } else if (exp >= 100000) {
    level = 7;
  } else if (exp >= 200000) {
    level = 8;
  } else if (exp >= 500000) {
    level = 9;
  } else if (exp >= 1000000) {
    level = 10;
  }
  
  return level;
});

const totalScore = computed(() => {
  return store.getters.totalScore;
});

// Active category
const activeCategory = ref('all');
</script>

